%button {
  border-radius: 2px;
  border-width: 0;
  cursor: pointer;
  display: block;
  margin: 1rem 0;
  min-width: 6.4rem;
  max-width: 100%;
  overflow: hidden;
  outline: none;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  transition: all .3s ease-in-out;
  & .mdi {
    font-size: inherit;
    margin-left: 1rem;
  }
}
%button-theme {
  background-color: $primary-color;
  @include shadow-1();
  color: #fff;
  font-weight: 300;
  &:hover {
    background-color: $primary-color-light;
    @include focus-shadow();
  }
}
.btn-flat {
  @extend %button;
  color: #3F51B5;
  background-color: transparent;
  font-weight: 500;
  line-height: 3.6rem;
  padding: 0 1.6rem;
}
.btn-small {
  @extend %button;
  @extend %button-theme;
  font-size: .8rem;
  padding: 0 .8rem;
  line-height: 2rem;
  min-width: 100px;
  max-width: 300px;
}
.btn {
  @extend %button;
  @extend %button-theme;
  line-height: 3.6rem;
  min-width: 100px;
  max-width: 300px;
  padding: 0 1.6rem;
}
.btn-large {
  @extend %button;
  @extend %button-theme;
  padding: 15px;
  width: 300px;
  font-size: 2rem;
}
.btn-block {
  @extend %button;
  @extend %button-theme;
  padding: 15px;
  width: 100%;
  font-size: 2rem;
}
.ripple {
  position: absolute;
  background: rgba(0,0,0,.25);
  border-radius: 100%;
  pointer-events: none;
  transform: scale(0);
  z-index: 2;
 }

 .ripple.show {
  animation: ripple .45s ease-out;
 }

 @keyframes ripple {
  to {
    transform: scale(1.5);
    opacity: 0;
  }
 }
